* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: 'Orbitron', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #7A3B69;
}
#root {
  height: 91vh;
  width: 100vw;
}
.App {
  height: 100%;
  width: 100vw;
}

#calculator {
  border: 10px solid #A9927D;
  width: 25%;
  height: 70%;
  margin: 5rem auto;
  display: flex;
  flex-direction: column;
  box-shadow: 10px 10px #00000070;
}

#display {
  height: 20%;
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
  overflow: auto;
}

#display-text {
  font-size: 2rem;
  color: #F2F4F3;
  margin-bottom: 0.5rem;
}

#button-wrapper {
  height: 80%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  background-color: #292828;
}

.button {
  border-radius: 10%;
  background-color: #8C2F39;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button:hover {
  background-color: #6e2129;
}

.button:active > .button-text {
  color: black;
}

.button-text {
  font-size: 2rem;
  color: #F2F4F3
}

#clear {
  grid-column: 1 / span 2;
}

#equals {
  grid-column: 4;
  grid-row: 4 / span 2;
}

#zero {
  grid-row: 5;
  grid-column: 1 / span 2;
}